'use client';
import { useState } from 'react';

interface District {
  id: number;
  name: string;
}

interface Panchayath {
  panchayathId: number;
  panchayathName: string;
  DstId: number;
  dstName: string;
}

interface Props {
  panchayaths: Panchayath[];
  districts: District[];
  onRefresh: () => void;
}

export default function PanchayathSection({ panchayaths, districts, onRefresh }: Props) {
  const [showModal, setShowModal] = useState(false);
  const [name, setName] = useState('');
  const [selectedDistrictId, setSelectedDistrictId] = useState(0);
  const [editId, setEditId] = useState<number | null>(null);

  const handleSave = async () => {
    if (!name.trim()) return alert('Enter panchayath name');
    if (!selectedDistrictId) return alert('Select a district');

    const method = editId ? 'PUT' : 'POST';
    const url = editId
      ? `https://localhost:7112/api/Panchayaths/${editId}`
      : `https://localhost:7112/api/Panchayaths`

    const body = JSON.stringify({
      id: editId || 0,
      name,
      DstId: selectedDistrictId,
      status: 1,
    });

    const res = await fetch(url, {
      method,
      headers: { 'Content-Type': 'application/json' },
      body,
    });

    if (res.ok) {
      setName('');
      setEditId(null);
      setSelectedDistrictId(0);
      setShowModal(false);
      onRefresh();
    } else alert('Failed to save panchayath');
  };

  const handleDelete = async (id: number) => {
    const res = await fetch(`https://localhost:7112/api/Panchayaths/${id}`, { method: 'DELETE' });
    if (res.ok) onRefresh();
  };

  return (
    <section>
      <div className="flex justify-between mb-4">
        <h2 className="text-xl font-bold">Panchayaths</h2>
        <button
          onClick={() => {
            setEditId(null);
            setName('');
            setSelectedDistrictId(0);
            setShowModal(true);
          }}
          className="bg-green-600 text-white px-4 py-2 rounded"
        >
          ➕ Add Panchayath
        </button>
      </div>
      <table className="w-full border">
        <thead className="bg-gray-200">
          <tr>
            <th className="p-2 text-left">ID</th>
            <th className="p-2 text-left">District</th>
            <th className="p-2 text-left">Name</th>
            <th className="p-2">Actions</th>
          </tr>
        </thead>
        <tbody>
          {panchayaths.map((p) => (
            <tr key={p.panchayathId} className="border-t">
              <td className="p-2">{p.panchayathId}</td>
              <td className="p-2">{p.dstName}</td>
              <td className="p-2">{p.panchayathName}</td>
              <td className="p-2 flex gap-2 justify-center">
                <button
                  className="bg-yellow-400 px-2 py-1 rounded"
                  onClick={() => {
                    setEditId(p.panchayathId);
                    setName(p.panchayathName);
                    setSelectedDistrictId(p.DstId);
                    setShowModal(true);
                  }}
                >
                  Edit
                </button>
                <button
                  className="bg-red-500 px-2 py-1 text-white rounded"
                  onClick={() => handleDelete(p.panchayathId)}
                >
                  Delete
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {showModal && (
        <div className="fixed inset-0 bg-black/30 flex justify-center items-center">
          <div className="bg-white p-6 rounded-lg w-full max-w-sm">
            <h3 className="text-lg font-semibold mb-4">
              {editId ? '✏️ Edit' : '➕ Add'} Panchayath
            </h3>
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="w-full border px-3 py-2 mb-4 rounded"
              placeholder="Panchayath Name"
            />
            <select
              value={selectedDistrictId}
              onChange={(e) => setSelectedDistrictId(Number(e.target.value))}
              className="w-full border px-3 py-2 mb-4 rounded"
            >
              <option value={0}>Select District</option>
              {districts.map((d) => (
                <option key={d.id} value={d.id}>
                  {d.name}
                </option>
              ))}
            </select>
            <div className="flex justify-end gap-2">
              <button onClick={() => setShowModal(false)} className="border px-4 py-2 rounded">
                Cancel
              </button>
              <button onClick={handleSave} className="bg-green-600 text-white px-4 py-2 rounded">
                Save
              </button>
            </div>
          </div>
        </div>
      )}
    </section>
  );
}
